section{
	/*border:1px solid black;*/
	height:120%;
	display:grid;
	justify-content: space-around;
	align-items: center;
	color:#FFFFF4;

	background-color: #36454F;
}
input{
	font-size:20px;
	font-family: 'Bruno Ace SC', cursive;
	text-align: center;
	letter-spacing: 2px;
	margin-top:5px;
	border-radius: 5px;
	width:80%;

}
button{

	height:60%;
	font-size:15px;
	font-family: 'Bruno Ace SC', cursive;
	border-radius: 12px;
	letter-spacing: 1px;
	width:80%;
	margin-left: 10%;

}
label{
	letter-spacing: 2px;
	margin-bottom: 5px; 
}
#submit_bttn{
	background-color: blue;
	color:white;

}